.head-subhead-btn {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 40px;
}

.head-subhead-wlc-scr {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.head-wlc-scr {
    width: 315px;
    line-height: 120%;
    margin: 0;
    font-weight: 400;
    font-size: 56px;
    color: #2D2D2D;
}

.subhead-wlc-scr {
    width: 450px;
    line-height: 160%;
    margin: 0;
    font-family: 'Mulish';
    font-weight: 400;
    font-size: 24px;
    color: #1F1249;
}

.btn {
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 450px;
    height: 60px;
    line-height: 140%;
    background: linear-gradient(113.87deg, #2663DF 14.81%, #758FF0 88.76%);
    box-shadow: 0px 6px 18px #9CADF2;
    border-radius: 10px;
    border: none;
    color: white;
    cursor: pointer;
    font-weight: 700;
    font-size: 16px;
}

.link {
    text-decoration: none;
    color: #FFFFFF;
    padding: 0 50px
}

@media (max-width: 375px) {
    .img {
        width: 315px;
        height: 315px;
    }

    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    .head-subhead-wlc-scr {
        gap: 10px;
        text-align: center;
    }

    .head-wlc-scr {
        font-size: 42px;
    }


    .subhead-wlc-scr {
        width: 315px;
        font-size: 16px;
    }

    .btn {
        width: 315px;
    }
}